@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,900&family=Open+Sans:wght@300;400;500;600;700;800&family=Russo+One&family=Tilt+Warp&display=swap');
* {
    font-size: calc(5px + 2vmin);
    margin: 0;
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
    /* font: 400 16px/24px Macan, Helvetica Neue, Helvetica, Arial, sans-serif; */
}

.split_me1 {
    display: flex;
    justify-content: center;
    width: 100%;
    /* flex-direction: row-reverse; */
}

.one_1 {
    /* border: 1px solid; */
    width: 10%;
    display: flex;
    justify-content: center;
}

.one_1 ul {
    height: 200px;
    background-color: #004225;
    border-radius: 10px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 0;
    padding: 15px;
    color: #ffffff;
    font-weight: 900;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.one_1 ul li {
    list-style-type: none;
    width: 100%;
    /* padding-bottom: 35px; */
}

.one_2 {
    /* border: 1px solid; */
    width: 45%;
}

.header11 {
    font-size: 25px;
    font-weight: 800;
    margin-bottom: 30px;
}

.tile {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    width: 100%;
    margin-bottom: 10px;
}

.topic_me {
    font-weight: 800;
}

.sub_me {
    font-size: small;
}

.p2 {
    text-align: center;
}

.location {
    width: 45%;
    display: flex;
    padding-left: 85px;
    padding-right: 85px;
    /* border: 1px solid; */
}

.place {
    width: 100%;
    height: 80%;
    display: flex;
}

.center1 {
    align-items: center;
    justify-content: center;
    /* border: 1px solid; */
    height: 500px;
    display: flex;
}


